/**
 * Loading component
 *
 * @since 1.0.0
 */
.apb-loading {
  display: grid;
  grid-auto-flow: column;
  align-items: center;
  justify-content: center;
  gap: var(--apb-bsu-half);
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: var(--apb-bsu-x2);
  text-align: center;
  font-weight: 600;
}